import { Component, OnInit, Input } from '@angular/core';
import { Client } from '../../../shared/client';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { ClientService } from '../../client.service';
import { TrainingentryService } from '../../Trainingentry.service';
import { TrainingService } from '../../Training.service';
import { PaymentService } from '../../payment.service';
import { Payment } from 'src/shared/payment';
import { FormBuilder, FormGroup, NgForm, Validators, FormControl } from '@angular/forms';
import { last, forEach } from '@angular/router/src/utils/collection';
import { of, Observable } from 'rxjs';
import { SidebarComponent } from 'src/app/sidebar/sidebar.component';
import { User } from 'src/shared/user';
import { Group } from 'src/shared/group';
import { Router } from '@angular/router';
import { SubscriptionService } from 'src/app/subscription.service';

@Component({
  selector: 'app-section-clients',
  templateUrl: './section-clients.component.html',
  styleUrls: ['./section-clients.component.css']
})
export class SectionClientsComponent implements OnInit {

  currentUser: User;
  role: string;

  constructor(private router: Router, private formbulider: FormBuilder, private subscriptionService: SubscriptionService, private httpService: HttpClient, private clientService: ClientService, private paymentService: PaymentService, private trainingService: TrainingService) {
    this.currentUser = JSON.parse(localStorage.getItem('currentUser'));

    this.getClients();                                   // using ClientService I simply get all the clients through API, and list them in table

    this.clientForm = new FormGroup({                    // also I initialize formgroup which is displayed, when user tries to edit row in table (text changes to input field on click of icon)
      name: new FormControl(),
      surname: new FormControl(),
      phone: new FormControl(),
      email: new FormControl(),
      group: new FormControl(),

      gymEntriesLeft: new FormControl(),
      martialArtsEntriesLeft: new FormControl(),
    });

  }
 

  showDetails = false;
  message = null;                         // displayed in console just for my information whats going on and where

  clients: Array<Client> = [];                 // array used for storing, clients received from API calls
  filteredClients: Array<Client> = [];
  client: any;
  clientById: Client;                     // client object used when getting one client by id  

  currentId = null;                       // when row is clicked, user assigned to this row (user's id) is stored in this variable, and parsed through ngOnDestroy to payment             
  editRowId = null;                       // when row is edited, user assigned to this row (user's id) is stored in this variable, and parsed through ngOnDestroy to payment             


  clientForm: FormGroup;
  filterClientsForm: any;
  groups: Array<Group> = [];

  today = new Date().toISOString();
  searchClient;                            // filter for searching clients id and other information in serach bar

  edit: boolean = true;

  p: number = 1;                          //pagination starting point

  index: number;


  ngOnInit() {

    this.filterClientsForm = this.formbulider.group({      // create form for filtering table of entries 

      GroupId: ['', [Validators.required]],

    });
    this.getGroups();
    if (this.currentUser != null) {
      this.role = this.currentUser.role;

    } 
    this.getGroups();
  }
  resetujFiltr() {
    this.getClients();
  }
  applyFilter() {

    let groupId = this.filterClientsForm.get('GroupId').value;
     
   
 
    for (let object of this.clients) {
       
      for (let contract of object.contracts) {
        if (contract.groupId == groupId && contract.isDeleted != true) { 
          this.filteredClients.push(object); 
        } 
      }
    }
   
    this.clients = this.filteredClients;
  }
  ngOnDestroy() {
    this.paymentService.currentId = this.currentId;         // a way of sending clicked user id to training or payment component
    this.trainingService.currentId = this.currentId;
  }
  getGroups() {
    this.subscriptionService.getGroups().subscribe(data => {
      this.groups = data;
      this.message = 'Pomyślnie pobrano spis grup.';
    });
  }
  getClients() {
    this.clientService.getClients().subscribe(data => {
      this.clients = data;
    });
  }

  deleteClient(id: number) {

    if (confirm("Czy jesteś pewien, że chcesz usunąć tego użytkownika?")) {
      this.clientService.deleteClient(id).subscribe(() => {


        this.getClients();
      });
    }

  }

  editClient(id: number) {

    if (this.editRowId == null) {
      // When edit icon clicked, and so far it wasn't clicked (form is not open => editRowId == null), I set the editRowId to current row's user Id,
      // and retrive that single user from api, and save him in ClientById variable

      this.editRowId = id;
      this.clientService.getClientById(this.editRowId).subscribe((clientData: Client) => {
        this.clientById = clientData;
      });
    }

    else if (this.editRowId != null) {
      this.editRowId = null;
      if (this.edit == true || this.edit == null) {
        this.edit = false;
      }
      else if (this.edit == false || this.edit == null) {
        this.edit = true;
      }
    }


  }

  // Function that catches event of clicking on user record, and saves his Id in variable currentId
  getId(Id: number) {

    // If row hasn't been clicked yet..
    if (this.currentId == null || this.currentId != Id) {
      this.currentId = Id;
    }
    // .. if already was clicked, set to null (unclick )
    else if (this.currentId == Id) {
      this.currentId = null;
    }

  }

  onFormSubmit(clientForm: any) {



    this.clientService.updateClient(this.clientForm, this.clientById).subscribe(() => {

      this.message = "Zaktualizowano dane klienta.";
 
      this.getClients();
      this.edit = false;
      this.editRowId = null;
    })

  }
  toggleDetailInfo() {

    this.showDetails = !this.showDetails;
  }



}

