import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Payment } from '../shared/payment';
import { Client } from '../shared/client';
import { Subscription } from '../shared/subscription';

import { Training } from '../shared/training';
import { TrainingSchedules } from 'src/shared/trainingschedule';
@Injectable({
  providedIn: 'root'
})
export class TrainingService {

  public currentId: number;
  url = 'http://localhost:8080/api/trainings/';
  trainingSchedules_url = 'http://localhost:8080/api/trainingschedules/';


  constructor(private http: HttpClient) { }

  getTrainings(): Observable<Array<Training>> {
    return this.http.get<Array<Training>>(this.url);
  }
  createTraining(training: Training): Observable<any> {
    return this.http.post<any>(this.url, training, { responseType: 'text' as 'json' });
  }
  deleteTraining(Id: number): Observable<number> {
    return this.http.delete<number>(this.url + Id ); 
  }

  getTrainingsSchedule(): Observable<Array<TrainingSchedules>> {
    return this.http.get<Array<TrainingSchedules>>(this.trainingSchedules_url);
  }
  addTrainingToSchedule(trainingRow: TrainingSchedules): Observable<any> {
    return this.http.post<any>(this.trainingSchedules_url, trainingRow, { responseType: 'text' as 'json' });
  }
  deleteTrainingSchedule(Id: number): Observable<number> {
    return this.http.delete<number>(this.trainingSchedules_url + Id);
  }
}
