import { Component, OnInit, Input, HostListener } from '@angular/core';
import { FormBuilder, FormGroup, NgForm, Validators, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { Payment } from '../../../shared/payment';
import { Client } from 'src/shared/client';
import { Subscription } from 'src/shared/subscription';
import { TrainingEntry } from 'src/shared/TrainingEntry';
import { Training } from 'src/shared/Training';

import { PaymentService } from '../../payment.service';
import { SubscriptionService } from '../../subscription.service';
import { ClientService } from '../../client.service';
import { InformationService } from '../../information.service';
import { TrainingentryService } from '../../TrainingEntry.service';
import { TrainingService } from '../../Training.service';

import { DatePipe } from '@angular/common';
import { MatSnackBar } from '@angular/material';
import { SidebarComponent } from 'src/app/sidebar/sidebar.component';
import { Information } from 'src/shared/information';
import { TrainingSchedules } from 'src/shared/trainingschedule';

 
@Component({
  providers: [SidebarComponent],
  selector: 'app-section-add-training-entry',
  templateUrl: './section-add-training-entry.component.html',
  styleUrls: ['./section-add-training-entry.component.css']
})
export class SectionAddTrainingEntryComponent implements OnInit {

 
  today = new Date((new Date()));
  todayIso = new Date().toISOString();

  dataSaved = false;
  message = null;
  cardMessage = "";

  addTrainingEntryForm: any;                                      // form used for entering training entry 
  currentId: number;                                              // selected client Id passed from clients component on destroy
  object: Client = null;
  trainingEntry: TrainingEntry;                                   // current training entry object
  trainings: TrainingSchedules[] = [];

  disclaimer: Information[] = [];  

  // array of available training objects
  pickedTraining: Training;                                       // training object selected by user
  currentClient: Client = null;                                          // current client object
      
  oneTimePayment: Payment = null;                                        // payment in case of one time entry
  compensatedPayment: Payment = null;                                        // payment in case of one time entry
                                   // compensate payment

  
  unusualEntry: boolean = null;                                   // boolean determining wheter entry is not realted with subscription - singular
  benefitEntry: boolean = null;                                   // boolean determining wheter entry is realted with one of benefits system

  benefits: string[] = ['Karta Multisport', 'OK System'];         // just an array with two choice benefits

  TodaysDate = new Date((new Date().getTime() + 3600000));                  // todays date 
  dayName: string;
  createdInformation: Information = null;
   
  constructor(private httpService: HttpClient,
    private formbulider: FormBuilder,
    private paymentService: PaymentService,
    private trainingService: TrainingService,
    private trainingentryService: TrainingentryService,
    private clientService: ClientService,
    private informationService: InformationService,
    private subscriptionService: SubscriptionService,
    private router: Router, public snackBar: MatSnackBar, private sidebarComponent: SidebarComponent) {

  }
 
  ngOnInit() {

    this.today = new Date(this.today.setHours(this.today.getHours() + 1));

    var dayNumber = this.TodaysDate.getDay()
    switch (dayNumber) {
      case 0:
        this.dayName = "Niedziela";
        break;
      case 1:
        this.dayName = "Poniedziałek";
        break;
      case 2:
        this.dayName = "Wtorek";
        break;
      case 3:
        this.dayName = "Środa";
        break;
      case 4:
        this.dayName = "Czwartek";
        break;
      case 5:
        this.dayName = "Piątek";
        break;
      case 6:
        this.dayName = "Sobota";
        break; 
    }

    this.getTrainings();

    this.trainings = this.trainings.filter(training => training.Day == this.dayName);
    
    // so they can be listed as a select list
    this.currentId = this.trainingService.currentId;                  // id and client values passed from client section
    this.GetClientById(this.currentId);
 

    this.getInformation();
   

    this.addTrainingEntryForm = this.formbulider.group({
      ClientId: this.currentId,
      TrainingId: ['', [Validators.required]],
      Locker: ['', [Validators.required]],
      FreeEntry: [''],
      EntryRegistered: [this.TodaysDate],
      TrainingPayment: [''],
      AdditionalInfo: [''],
      OtherEntryType: [''],
    });

  }
  hideInformation(informationId: number, information: Information) {

  
    if (confirm("Czy chcesz usunąć uwagę?")) {
       
      information.display = false;
      this.informationService.updateInformation(informationId, information).subscribe(() => {
      
      });
       
    }
  
    this.getInformation();
    this.GetClientById(this.currentId);
  }

  compensatePayment(payment: Payment, cashRequired: number) {
    // When clients compensate not completed payment two following things happen:
    // his not completed payment is now completed, cashRegistered doesn't change
    // and new payment is generated with cashRegistered equal to the difference which should've been registered in the first place
    //let paymentObject = JSON.parse(payment.toString());
    //console.log(paymentObject);
     
    this.compensatedPayment = {
      subscriptionExpires: null,
      cashRegistered: (cashRequired - payment.cashRegistered),
      paymentRegistered: this.today,
      clientId: payment.clientId,
      additionalInformation: "Nadpłata za nieopłacony karnet",
      subscriptionId: null,
      paymentCompleted: true
      
    };
   

 

    if (confirm("Czy potwierdzasz, że klient wyrównał płatność?")) {
      payment.paymentCompleted = true;
      this.paymentService.updatePayment(payment.id, payment).subscribe(() => {
        this.message = "Status nieopłaconej płatności został zmieniony.";
    
      });
 

      this.paymentService.createPayment(this.compensatedPayment).subscribe(() => {
        this.message = "Dodano płatność kompensującą niedopłatę.";
         
      });


    }
  }
 
  AddTrainingEntry(trainingEntry: TrainingEntry) {
    // when client pays for individual entry we have to register payment

   
    if (this.unusualEntry == true) {


      this.oneTimePayment = {
        subscriptionExpires: null,
        cashRegistered: this.addTrainingEntryForm.get('TrainingPayment').value ,
        paymentRegistered: this.today,
        clientId: this.currentId,
        additionalInformation: "Wejscie pojedyncze - trening" + this.addTrainingEntryForm.get('TrainingId').value , 
        subscriptionId: null,
        paymentCompleted: true

      };

      this.paymentService.createPayment(this.oneTimePayment).subscribe(() => {
       });
      
    }

    if (this.unusualEntry == true) {
      trainingEntry.otherEntryType = "Wejście pojedyncze";
      trainingEntry.freeEntry = true;
    }
    if (this.benefitEntry == true) {
      trainingEntry.freeEntry = true;
    }
    this.trainingentryService.addTrainingEntry(trainingEntry).subscribe(() =>{
        this.message = 'Pomyślnie dodano wejście na trening.';
        this.addTrainingEntryForm.reset();
        this.openSnackBar(this.message, 'Ok!');
        this.router.navigate(['clients']); 
       
      }
    );

   }
  deleteContract(id:number) {
    if (confirm("Czy jesteś pewien, że chcesz usunąć tego użytkownika z grupy?")) {
      this.paymentService.deleteContract(id).subscribe(() => {
         
 
      });
    }
    this.GetClientById(this.currentId);
   

  }
  addInformation(additionalInfo?: string) {

    if (additionalInfo == null) {
      var Uwaga = prompt("Treść uwagi wyświetlanej przy każdorazym wejściu na trening");

    }
    if (additionalInfo != null) {
      var Uwaga = additionalInfo;
    }
   
    this.disclaimer = [];

    if (Uwaga != null) {
      this.createdInformation = {
        ClientId: this.currentId,
        display: true,
        Content: Uwaga
      };


      this.informationService.addInformationToClient(this.createdInformation).subscribe(
        () => {
          this.dataSaved = true;
        }
      );
      this.getInformation();
    }
   
  }
  

  getTrainings() {
    this.trainingService.getTrainingsSchedule().subscribe((trainingsData: TrainingSchedules[]) => {
      this.trainings = trainingsData;
   
    });
  }
  getInformation() {
    this.informationService.getInformationByClientId(this.currentId).subscribe(informationData  => {
      this.disclaimer = informationData;
      this.message = 'Pomyślnie pobrano spis uwag.';
    })
  }  
 
  resetForm() {
    this.addTrainingEntryForm.reset();
    this.message = null;
    this.dataSaved = false;
  }
 
  openSnackBar(message: string, action: string) {         // simply displaying snackbar with proper message
    this.snackBar.open(this.message, action, {
      duration: 4000,
      panelClass: ['snackbar']
    });
  }

  setUnusualEntry() {                                    // changes checbox value
    this.unusualEntry = !this.unusualEntry;
  }
  setBenefitEntry() {                                    // changes checbox value
    this.benefitEntry = !this.benefitEntry;
  }
  onFormSubmit() {
    const trainingEntry = this.addTrainingEntryForm.value;


    this.AddTrainingEntry(trainingEntry);
    this.addTrainingEntryForm.reset();
  }

  GetClientById(clientId: number) {
    this.clientService.getClientById(clientId).subscribe((clientData: any) => {

      this.currentClient = clientData;
 
     // console.log(this.currentClient);

      this.object = JSON.parse(this.currentClient.toString());
 
      //this.currentClient.subscriptionExpires = new Date(this.currentClient['subscriptionExpires'])  // catching users subscription date and transforming so they can be comparable

      this.object['subscriptionExpires'] = new Date(this.object['subscriptionExpires']);
      // 0 - wygasla 1 - dziala 2 - zaraz wygasnie

    //this.currentClient.SubscriptionExpired = 1;

      this.object['SubscriptionExpired'] = 1;

    

      if (this.object['gymEntriesLeft'] == 0 && this.object['martialArtsEntriesLeft'] == 0) {
        this.object['SubscriptionExpired'] = 0;
        this.cardMessage = " - WYKORZYSTANO LIMIT WEJŚĆ"
      } 
      else if (this.object['subscriptionExpires'] < this.today) {
         this.object['SubscriptionExpired'] = 0;
        this.cardMessage = this.cardMessage + " - UPŁYNĄŁ TERMIN WAŻNOŚCI";
      }
      else if (this.object['subscriptionExpires'].getTime() - this.today.getTime() <= 604800000)
      {
         this.object['SubscriptionExpired'] = 2;
        this.cardMessage = "KARNET WAŻNY - ZBLIŻA SIĘ TERMIN WAŻNOŚCI, POZOSTAŁO " + Math.round((this.object['subscriptionExpires'].getTime() - this.today.getTime()) / 86400000) + " DNI";
      }
      else if (this.object['subscriptionExpires'].getTime()  - this.today.getTime() <= 0)
      {
        this.object['SubscriptionExpired']  = 0;
        this.cardMessage = this.cardMessage + " - UPŁYNĄŁ TERMIN WAŻNOŚCI";
      }
       
    
     
    });
     
  }

   
}
