import { Component, OnInit, Input, HostListener } from '@angular/core';
import { Training } from '../../shared/training';
import { TrainingEntry } from '../../shared/trainingentry';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { TrainingService } from '../training.service';
import { TrainingentryService } from '../trainingentry.service'; 
import { MatTooltipModule } from '@angular/material/tooltip';
import { User } from 'src/shared/user';
import { SectionAddTrainingEntryComponent } from '../sections/section-add-training-entry/section-add-training-entry.component';
import { Observable } from 'rxjs';
import { Router } from '@angular/router'; 
import { TrainingSchedules } from 'src/shared/trainingschedule';
import { Timestamp } from 'rxjs/internal/operators/timestamp';
 

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css'],
  providers: [TrainingentryService  ]
})
export class SidebarComponent implements OnInit {
  
  currentUser: User;
  role: string;
  _subscription: any;

  message: string;
   
   

  constructor( private router: Router,private httpService: HttpClient, private trainingService: TrainingService, private trainingentryService: TrainingentryService) {
    this.currentUser = JSON.parse(localStorage.getItem('currentUser'));
  }

 
  dateToday = new Date((new Date().getTime()));
  dayName: string;

  trainingEntries: Array<TrainingEntry> = [];   
  trainingsSchedule: Array<TrainingSchedules> = [];

  ngOnInit() {

    var dayNumber = this.dateToday.getDay()
  

    switch (dayNumber) {
      case 0:
        this.dayName = "Niedziela";
        break;
      case 1:
        this.dayName = "Poniedziałek";
        break;
      case 2:
        this.dayName = "Wtorek";
        break;
      case 3:
        this.dayName = "Środa";
        break;
      case 4:
        this.dayName = "Czwartek";
        break;
      case 5:
        this.dayName = "Piątek";
        break;
      case 6:
        this.dayName = "Sobota";
        break;
      
    }


     
    this.getTrainingEntries();
    this.getTrainingsSchedule()

  
    if (this.currentUser != null) {
      this.role = this.currentUser.role;

    }
    

    this.trainingentryService.getTrainingEntries();
     
  }
 
  returnLocker(Id: number, trainingEntry: TrainingEntry) {

   
    this.trainingentryService.updateTrainingEntry(Id, trainingEntry).subscribe(() => {
     
    
      trainingEntry.lockerReturned = true;
      this.ngOnInit();
    });
  }

  getTrainingEntries() {
    this.trainingentryService.getTrainingEntries().subscribe(v => {
      this.trainingEntries = JSON.parse(v.toString());

      this.trainingEntries = this.trainingEntries.filter((v) => v.lockerReturned === false);
      this.trainingEntries = this.trainingEntries.filter((v) => v.locker != null);

    });

  }
  getTrainingsSchedule() {
    this.trainingService.getTrainingsSchedule().subscribe(data => {
      this.trainingsSchedule = data;
      this.message = 'Pomyślnie pobrano spis treningów.';

    });
  }
 
}
