import { Component, OnInit } from '@angular/core';
import { User } from 'src/shared/user';

import { AuthService } from '../auth.service';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
  currentUser: User;
  message  = '';
  snackbarClass = '';
  test: boolean;
  constructor(private authService: AuthService, private router: Router, public snackBar: MatSnackBar) { this.currentUser = JSON.parse(localStorage.getItem('currentUser'));}

  ngOnInit() {
  }
  openSnackBar(message: string, action: string) {
    this.snackBar.open(this.message, action, {
      duration: 4000,
      panelClass: [this.snackbarClass]
    });
  } 
  logout() {
    this.snackbarClass = 'snackbar';
    this.message =  "Pomyślnie się wylogowałeś!";
    this.authService.logout();

    this.openSnackBar(this.message, 'Ok!');
    this.router.navigate(['login']);
  }

}
