import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { Client } from 'src/shared/client';
import { ClientService } from 'src/app/client.service';
import { NgForm } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { DatePipe } from '@angular/common';
import { Router } from "@angular/router";
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-section-add-client',
  templateUrl: './section-add-client.component.html',
  styleUrls: ['./section-add-client.component.css']
})
export class SectionAddClientComponent implements OnInit {
  dateToday = new Date((new Date().getTime()));
  dataSaved = false;
  clientForm: any;
  clientIdUpdate = null;
  message = null;
  clients: string[];
  errorMessage = '';


  snackbarClass = '';
  constructor(private httpService: HttpClient, private formbulider: FormBuilder, private clientService: ClientService, private router: Router, public snackBar: MatSnackBar) { }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(this.message, action, {
      duration: 4000,
      panelClass: [this.snackbarClass]
    });
  } 

  getClients() {
    this.httpService.get('http://localhost:8080/api/clients').subscribe(data => {
      this.clients = data as string[];
    })
  }

  ngOnInit() {
    this.getClients();
    this.clientForm = this.formbulider.group({
      CardId: ['', [Validators.required]],
      Name: ['', [Validators.required]],
      Surname: ['', [Validators.required]],
      Phone: [''], 
      Email: [''], 
      ClientJoined: new FormControl(this.dateToday),
     
    });

    
  }

  ngOnDestroy() {
    this.clientService.getClients();
    
  }

  onFormSubmit() {
    this.dataSaved = false;
    const client = this.clientForm.value;
    this.CreateClient(client);
 
     
  }


  CreateClient(client: Client) {
    if (this.clientIdUpdate == null) {
      this.clientService.createClient(client).subscribe(
        () => {
          this.dataSaved = true;
          this.message = 'Klient został pomyślnie dodany. Nadaj mu karnet, w panelu klienci.';

          this.clientIdUpdate = null;
          
          this.getClients();
          this.snackbarClass = 'snackbar';
          this.openSnackBar(this.message, 'Ok!');
          this.router.navigate(['clients']);
        },
        (error) => {
          this.errorMessage = error.message;
          this.message = 'Nie można dodać klienta o tym Id, ponieważ istnieje ono już w bazie.';
          this.snackbarClass = 'snackbar-failed';
          this.openSnackBar(this.message, 'Ok!');
        }
      );
    }

  }


  resetForm() {
    this.clientForm.reset();
    this.message = null;
    this.dataSaved = false;
  }




}
