import { Component, OnInit } from '@angular/core';
import { Subscription } from '../../../shared/subscription';
import { Client } from '../../../shared/client';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { SubscriptionService } from '../../subscription.service';
import { Observable } from 'rxjs';
import { MatSnackBar } from '@angular/material';
import { ClientService } from 'src/app/client.service'; 
import { Chart } from 'chart.js';
import { Router } from '@angular/router';
import { User } from 'src/shared/user';
import { FormGroup, FormBuilder, FormControl } from '@angular/forms';
import { Group } from 'src/shared/group';


@Component({
  selector: 'app-section-subscriptions',
  templateUrl: './section-subscriptions.component.html',
  styleUrls: ['./section-subscriptions.component.css']
})
export class SectionSubscriptionsComponent implements OnInit {
  currentUser: User;


  constructor(private router: Router, private formbulider: FormBuilder, private httpService: HttpClient, private subscriptionService: SubscriptionService, public snackBar: MatSnackBar, private clientService: ClientService) {
    this.currentUser = JSON.parse(localStorage.getItem('currentUser'));
  }
 
 

  dataSaved = false;
  message = null;
  errorMessage = '';
  snackbarClass = '';

  subscriptionsData: any[];  // Array storing all types of subscriptions
  groupsData: any[];  // Array storing all types of subscriptions
  clients: Client[] = [];  // Array storing all types of subscriptions



  ngOnInit() {
    this.getSubscriptions();
    this.getGroups();
    //this.getClients();
 
 
  }

  getSubscriptions() {
  
    this.subscriptionService.getSubscriptions().subscribe(data => {
      this.subscriptionsData = data;

    });
    
  }
  getGroups() {

    this.subscriptionService.getGroups().subscribe(data => {
      this.groupsData = data;
 
    });

  } 
  editSubscription(subscriptionId: number, subscription: Subscription) {
     
    if (confirm("Czy chcesz edytować karnet?")) {


      subscription.name = prompt("Podaj nową nazwę karnetu");
      subscription.price = parseInt(prompt("Podaj nową cenę"));
      subscription.gymEntries = parseInt(prompt("Podaj ilość możliwych, miesięcznych wejść na siłownię"));
      subscription.martialArtsEntries = parseInt(prompt("Podaj ilość możliwych, miesięcznych wejść na sporty walki"));


      this.subscriptionService.updateSubscription(subscriptionId, subscription).subscribe(() => {

      });

    }

  }
  editGroup(groupId: number, group: Group) {

   
    if (confirm("Czy chcesz edytować grupę?")) {
      const name = prompt("Podaj nową nazwę grupy");
      const grupaDlaUmow = prompt("Czy od grupy wymagać płatności ? [tak/nie]");

      group.name = name;

      if (grupaDlaUmow.substring(0, 1).toLowerCase() == 't') {
         
        group.contractRelatedGroup = true;
      }
      else {
         
        group.contractRelatedGroup = false;
      }

      this.subscriptionService.updateGroup(groupId, group).subscribe(() => {

      });

    }
     
  }


    
  deleteSubscription(Id: number) {
    this.subscriptionService.deleteSubscription(Id).subscribe(() => {
      this.dataSaved = true;
      this.message = 'Pomyślnie usunięto rodzaj karnetu.';
      this.ngOnInit();
    },
      (error) => {
        this.errorMessage = error.message;
        this.message = 'Karnet do którego są przypisani klienci, nie może zostać usunięty.';
        this.snackbarClass = 'snackbar-failed';
        this.openSnackBar(this.message, 'Ok!');
      });
  }
  deleteGroup(Id: number) {
    console.log(Id);
    this.subscriptionService.deleteGroup(Id).subscribe(() => {
      this.dataSaved = true;
      this.message = 'Pomyślnie usunięto grupę.';
      this.ngOnInit();
    },
      (error) => {
        this.errorMessage = error.message;
        this.message = 'Grupa do której są przypisani klienci, nie może zostać usunięta.';
        this.snackbarClass = 'snackbar-failed';
        this.openSnackBar(this.message, 'Ok!');
      });
  }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(this.message, action, {
      duration: 4000,
      panelClass: [this.snackbarClass]
    });
  } 
}

