import { Routes, RouterModule } from '@angular/router';
import { SectionClientsComponent } from './app/sections/section-clients/section-clients.component';
import { SectionTrainingsComponent } from './app/sections/section-trainings/section-trainings.component';
import { SectionSubscriptionsComponent } from './app/sections/section-subscriptions/section-subscriptions.component';
import { SectionAddClientComponent } from './app/sections/section-add-client/section-add-client.component';
import { SectionAddSubscriptionComponent } from './app/sections/section-add-subscription/section-add-subscription.component';
import { SectionAcceptPaymentComponent } from './app/sections/section-accept-payment/section-accept-payment.component';
import { SectionPaymentsComponent } from './app/sections/section-payments/section-payments.component';
import { SectionAddTrainingEntryComponent } from './app/sections/section-add-training-entry/section-add-training-entry.component';
import { ModuleWithProviders } from '@angular/core';
import { LoginComponent } from './app/login (2)/login.component';

export const appRoutes: Routes = [
  { path: 'clients', component: SectionClientsComponent },
  { path: 'trainings', component: SectionTrainingsComponent},
  { path: 'subscriptions', component: SectionSubscriptionsComponent },
  { path: 'payments', component: SectionPaymentsComponent },
  { path: 'add-client', component: SectionAddClientComponent },
  { path: 'add-subscription', component: SectionAddSubscriptionComponent },
  { path: 'add-training-entry', component: SectionAddTrainingEntryComponent }, 
  { path: 'accept-payment', component: SectionAcceptPaymentComponent}, 
  { path: 'login', component: LoginComponent }, 
  { path: '', redirectTo: '/login', pathMatch: 'full'  }
];

export const AppRoutingModule: ModuleWithProviders = RouterModule.forRoot(appRoutes);
