import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Information } from '../shared/information';
import { HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class InformationService {

  url = 'http://localhost:8080/api/information/';

  constructor(private http: HttpClient) { }

  getInformations(): Observable<Array<Information>> {
    return this.http.get<Array<Information>>(this.url);
  }
  
  getInformationByClientId(Id: number): Observable<Array<Information>> {
    return this.http.get<Array<Information>>(this.url + Id);
  }
  addInformationToClient(information: Information): Observable<any> {
    return this.http.post<any>(this.url, information, { responseType: 'text' as 'json' });
  }
  updateInformation(id: number, information: Information): Observable<Information> {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };
    information.display = false;
    return this.http.put<Information>(this.url + id, information, httpOptions);
  }
}
