import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Subscription } from '../shared/subscription';
import { Group } from '../shared/group';
import { HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators'; 

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class SubscriptionService {

  url = 'http://localhost:8080/api/subscriptions/';
  group_url = 'http://localhost:8080/api/groups/';
  constructor(private http: HttpClient) { }
 
  getSubscriptions(): Observable<Array<Subscription>> {
    return this.http.get<Array<Subscription>>(this.url);
  }
  GetSubscriptionById(Id: number): Observable<any> {
    return this.http.get(this.url + Id)
      .pipe(map((response: Response) => <any>response.json()));
  } 
   
  createSubscription(subscription: Subscription): Observable<any> {
    return this.http.post<any>(this.url, subscription, { responseType: 'text' as 'json' });
  }
  deleteSubscription(Id: number){ 
    return this.http.delete(this.url + Id);
  }
  getGroups(): Observable<Array<Group>> {
    return this.http.get<Array<Group>>(this.group_url);
  }
  createGroup(group: Group): Observable<any> {
    return this.http.post<any>(this.group_url, group, { responseType: 'text' as 'json' });
  }
  deleteGroup(Id: number) {
     
    return this.http.delete(this.group_url + Id);
  }

  updateGroup(id: number, group: Group ): Observable<Group> {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };
   
    return this.http.put<Group>(this.group_url + id, JSON.stringify(group), httpOptions
    );
     
  }
  updateSubscription(id: number, subscription: Subscription): Observable<Subscription> {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };

    return this.http.put<Subscription>(this.url + id, JSON.stringify(subscription), httpOptions
    );

  }

}
