import { Component, OnInit, Input } from '@angular/core';
import { Training } from '../../../shared/training';
import { TrainingEntry } from '../../../shared/trainingentry';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { TrainingService } from '../../training.service';
import { TrainingentryService } from '../../trainingentry.service';
import { MatListModule } from '@angular/material/list';
import { FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { FormControl } from '@angular/forms';
import { forEach } from '@angular/router/src/utils/collection';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { TrainingSchedules } from 'src/shared/trainingschedule';

@Component({
  selector: 'app-section-trainings',
  templateUrl: './section-trainings.component.html',
  styleUrls: ['./section-trainings.component.css'],


})
export class SectionTrainingsComponent implements OnInit {



  constructor(private httpService: HttpClient, private formbulider: FormBuilder, private trainingService: TrainingService, private trainingentryService: TrainingentryService) { }


  trainingId: number;

  dataSaved = false;
  message = null;
  trainingEntriesAmount: number = 0;

  trainings: Array<Training> = [];
  trainingsSchedule: Array<TrainingSchedules> = [];
  trainingEntries: Array<TrainingEntry> = [];
  filteredtrainingEntries: Array<TrainingEntry> = [];



  today = new Date(new Date().getTime());

  fromDate = new Date(this.today.getFullYear(), this.today.getMonth(), this.today.getDate(), 0, 0, 0);
  toDate = new Date(this.today.getFullYear(), this.today.getMonth(), this.today.getDate(), 23, 59, 59);

  filterForm: any;
  trainingForm: any;
  trainingScheduleForm: any;

  types: string[] = ['SPORTY WALKI', 'SIŁOWNIA'];
  daysOfWeek: string[] = ['Poniedziałek', 'Wtorek', 'Środa', 'Czwartek', 'Piątek', 'Sobota', 'Niedziela'];

  getTrainings() {
    this.trainingService.getTrainings().subscribe(data => {
      this.trainings = data;
      this.message = 'Pomyślnie pobrano spis treningów.';

    });
  }

  getTrainingsSchedule() {
    this.trainingService.getTrainingsSchedule().subscribe(data => {
      this.trainingsSchedule = data;
     
      this.message = 'Pomyślnie pobrano spis treningów.';

    });
  }
  getTrainingEntries() {
    this.trainingentryService.getTrainingEntries().subscribe(trainingentriesData => {
      this.trainingEntries = trainingentriesData;
      this.message = 'Pomyślnie pobrano spis treningów.';
    });
  }



  ngOnInit() {

    this.getTrainings();          // get data to fill Trainings list and entries table
    this.getTrainingsSchedule();

  
    this.trainingForm = this.formbulider.group({    // create form for creating new training
      Name: ['', [Validators.required]],
      Type: ['', [Validators.required]],

    });

    this.trainingScheduleForm = this.formbulider.group({    // create form for creating new training
      Day: ['', [Validators.required]],
      HourStarts: ['', [Validators.required]],
      HourFinishes: ['', [Validators.required]],
      TrainingId: ['', [Validators.required]],

    });

    this.filterForm = this.formbulider.group({      // create form for filtering table of entries 
      toDate: [this.toDate],
      fromDate: [this.fromDate],
      trainingId: ['', [Validators.required]],

    });

    this.trainingEntriesAmount = 0;
  
    this.httpService.get('http://localhost:8080/api/trainingEntries').subscribe((trainingEntriesData: TrainingEntry[]) => {
      this.trainingEntries = trainingEntriesData;
      this.filteredtrainingEntries = this.trainingEntries;
      for (var i = 0; i < this.trainingEntries.length; i++) {

        this.trainingEntriesAmount++;
      }
      this.applyFilter();

    });


  }
  onFormSubmit() {
    this.dataSaved = false;
    const training = this.trainingForm.value;
    this.CreateTraining(training);
    this.trainingForm.reset();
  }
  onScheduleFormSubmit() {
    this.dataSaved = false;
    const trainingScheduleRow = this.trainingScheduleForm.value;
    
    this.CreateTrainingScheduleRow(trainingScheduleRow);
    this.trainingScheduleForm.reset();
  }


  CreateTraining(training: Training) {

    console.log(training);
    this.trainingService.createTraining(training).subscribe(
      () => {
        this.dataSaved = true;
        this.message = 'Pomyślnie utworzono nowy karnet.';

        this.getTrainings();

      }
    );
  }

  CreateTrainingScheduleRow(trainingScheduleRow: TrainingSchedules) {
    this.trainingService.addTrainingToSchedule(trainingScheduleRow).subscribe(
      () => {

        this.getTrainingsSchedule();
      }
    );

   
  }

  deleteTraining(Id: number) {
    if (confirm("Czy jesteś pewien, że chcesz usunąć trening?")) {
      this.trainingService.deleteTraining(Id).subscribe(() => {

        this.message = 'Pomyślnie usunięto trening.';
        this.ngOnInit();
      });
    }

  }
  
  deleteTrainingSchedule(Id: number) {
    if (confirm("Czy jesteś pewien, że chcesz usunąć trening?")) {
      this.trainingService.deleteTrainingSchedule(Id).subscribe(() => {

        this.message = 'Pomyślnie usunięto trening.';
        this.ngOnInit();
      });
    }

  }

  applyFilter() {
    this.trainingEntriesAmount = 0;
    this.filteredtrainingEntries = this.trainingEntries;
    this.fromDate = this.filterForm.get('fromDate').value;
    this.toDate = this.filterForm.get('toDate').value;
    this.trainingId = this.filterForm.get('trainingId').value;

    this.httpService.get('https://localhost:8080/api/trainingEntries').subscribe((trainingEntriesData: TrainingEntry[]) => {
      this.trainingEntries = trainingEntriesData;
    });

    this.filteredtrainingEntries = this.trainingEntries.filter(p => new Date(p.entryRegistered).getTime() >= this.fromDate.getTime() && new Date(p.entryRegistered).getTime() <= this.toDate.getTime());

    if (this.trainingId != 0) {
      this.filteredtrainingEntries = this.trainingEntries.filter(p => new Date(p.entryRegistered).getTime() >= this.fromDate.getTime() && new Date(p.entryRegistered).getTime() <= this.toDate.getTime() && p.trainingId == this.trainingId);
    }


    for (var i = 0; i < this.filteredtrainingEntries.length; i++) {
      this.trainingEntriesAmount++;
    }


    for (var i = 0; i < this.trainings.length; i++) {
      var count = this.filteredtrainingEntries.filter((obj) => obj.trainingId === this.trainings[i]['id']).length;
      const trainingsAnalytics: (string | number)[] = [count, this.trainings[i]['name']];
   

    }


  }



}
