import { Component, OnInit } from '@angular/core';
import { Payment } from '../../../shared/payment';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { PaymentService } from '../../payment.service';
import { Observable, from } from 'rxjs';

import { SubscriptionService } from '../../subscription.service';
import { ClientService } from '../../client.service';
import { Subscription } from '../../../shared/subscription';
import { Client } from '../../../shared/client';
import { FormBuilder, Validators, FormControl, FormGroup } from '@angular/forms';
import { MatTable, MatTableDataSource } from '@angular/material';
import * as jspdf from 'jspdf';
import html2canvas from 'html2canvas';
import { FilterPipe } from 'ngx-filter-pipe';
import { User } from 'src/shared/user';
import { Router } from '@angular/router';
import { Group } from 'src/shared/group';

@Component({
  selector: 'app-section-payments',
  templateUrl: './section-payments.component.html',
  styleUrls: ['./section-payments.component.css']
})
export class SectionPaymentsComponent implements OnInit {
  message: string;

  compensatedPayment: Payment = null;
  currentUser: User;

  constructor(private router: Router, private httpService: HttpClient, private paymentService: PaymentService, private formbulider: FormBuilder, private subscriptionService: SubscriptionService) { this.currentUser = JSON.parse(localStorage.getItem('currentUser')); }




  today = new Date(new Date().getTime());

  fromDate = new Date(this.today.getFullYear(), this.today.getMonth(), this.today.getDate(), 0, 0, 0);
  toDate = new Date(this.today.getFullYear(), this.today.getMonth(), this.today.getDate(), 23, 59, 59);
  subscriptionId: number;
  groupId: number;

  payments: Array<Payment> = [];
  subscriptions: Array<Subscription> = [];
  groupsData: Array<Group> = [];
  filteredPayments: Array<Payment> = [];
  // list for storing payments

  // list for storing payments

  sum: number = 0;                // variable for storing sum of currently displayed payments list
  paymentsAmount: number = 0;                // variable for storing sum of currently displayed payments list
  expectedSum: number = 0;


  filterForm: any;
  userFilter: string;


  compensatePayment(payment: Payment, cashRequired: number) {
    // When clients compensate not completed payment two following things happen:
    // his not completed payment is now completed, cashRegistered doesn't change
    // and new payment is generated with cashRegistered equal to the difference which should be registered


    if (confirm("Czy potwierdzasz, że klient wyrównał płatność?")) {

      this.compensatedPayment = {

        subscriptionExpires: null,
        cashRegistered: (cashRequired - payment.cashRegistered),
        paymentRegistered: this.today,
        clientId: payment.clientId,
        additionalInformation: "Nadpłata za nieopłacony karnet",
        subscriptionId: null,
        paymentCompleted: true

      };
      payment.paymentCompleted = true;
      this.paymentService.updatePayment(payment.id, payment).subscribe(() => {
        this.message = "Status nieopłaconej płatności został zmieniony.";
       

        this.paymentService.createPayment(this.compensatedPayment).subscribe(() => {
          this.message = "Dodano płatność kompensującą niedopłatę.";
       

        });

      });



    }

  }


  ngOnInit() {

    if (this.currentUser.role != 'admin' || this.currentUser.role == null || this.currentUser == null) {


      this.router.navigate(['login']);
    }
    this.getSubscriptions();
    this.getGroups();
    this.sum = 0;
    this.paymentsAmount = 0;

    this.filterForm = this.formbulider.group({
      toDate: [this.toDate],
      fromDate: [this.fromDate],
      subscriptionId: [''],
 
    });


    this.toDate = new Date(this.toDate.getFullYear(), this.toDate.getMonth(), this.toDate.getDate(), 23, 59, 59);
    this.fromDate = new Date(this.fromDate.getFullYear(), this.fromDate.getMonth(), this.fromDate.getDate(), 0, 0, 0);


    this.paymentService.getPayments().subscribe(paymentsData => {
      this.payments = paymentsData;
      this.filteredPayments = this.payments;

      for (var i = 0; i < this.payments.length; i++) {
        this.sum += this.filteredPayments[i].cashRegistered;
        this.paymentsAmount++;
      }


      this.applyFilter();

    });


  }


  applyFilter() {
    this.sum = 0;
    this.paymentsAmount = 0;
    this.filteredPayments = this.payments;
    this.fromDate = this.filterForm.get('fromDate').value;
    this.toDate = this.filterForm.get('toDate').value;
    this.subscriptionId = this.filterForm.get('subscriptionId').value;



    this.paymentService.getPayments().subscribe(paymentsData => {
      this.payments = paymentsData;
       
    });

    

    this.filteredPayments = this.payments.filter(p => new Date(p.paymentRegistered).getTime() >= this.fromDate.getTime() && new Date(p.paymentRegistered).getTime() <= this.toDate.getTime());

    if (this.subscriptionId != 0) {
      this.filteredPayments = this.payments.filter(p => new Date(p.paymentRegistered).getTime() >= this.fromDate.getTime() && new Date(p.paymentRegistered).getTime() <= this.toDate.getTime() && p.subscriptionId == this.subscriptionId);
    }



    for (var i = 0; i < this.filteredPayments.length; i++) {
      this.sum += this.filteredPayments[i].cashRegistered;
      this.paymentsAmount++;
    }
  }


  getSubscriptions() {
    this.subscriptionService.getSubscriptions().subscribe(subscriptionsData => {
      this.subscriptions = subscriptionsData;
      this.message = 'Pomyślnie pobrano spis karnetów.';
    });
  }
  getGroups() {

    this.subscriptionService.getGroups().subscribe(data => {
      this.groupsData = data;
      this.message = 'Pomyślnie pobrano spis karnetów.';


    });

  }
}
