import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { Observable, BehaviorSubject } from 'rxjs';
import { Payment } from '../shared/payment';
import { TrainingEntry } from '../shared/trainingentry';
import { Client } from '../shared/client';
import { Subscription } from '../shared/subscription';
import { SidebarComponent } from './sidebar/sidebar.component';
 
import { map } from 'rxjs/operators';
import { EventEmitter } from 'events';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class TrainingentryService {

  trainingEntries: TrainingEntry[] = [];   

  public currentId: number;

  url = 'http://localhost:8080/api/trainingentries/';
  constructor(private http: HttpClient) { }
  
  getTrainingEntries(): Observable<any>  {
    return this.http.get(this.url, { responseType: 'text' as 'json' });
  } 
  addTrainingEntry(trainingEntry: any): Observable<any> {
     
 
    return this.http.post<any>(this.url, trainingEntry, { responseType: 'text' as 'json' });
     
  }
  updateTrainingEntry(id: number, trainingEntry: any): Observable<any> {
    
    console.log(id);
    console.log(trainingEntry);
    trainingEntry.lockerReturned = true;
    return this.http.put<any>(this.url + id, trainingEntry, { responseType: 'text' as 'json' });
  }
}
