import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Payment } from '../shared/payment';
import { Contract } from '../shared/contract';
import { Client } from '../shared/client';
import { Subscription } from '../shared/subscription';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class PaymentService {

  public currentId: number;

  url = 'http://localhost:8080/api/payments/';
  url_contracts = 'http://localhost:8080/api/contracts/';

  constructor(private http: HttpClient) { }


  getPayments(): Observable<Array<Payment>> {
    return this.http.get<Array<Payment>>(this.url);
  }

  createPayment(payment: Payment): Observable<any> {
   
   
    return this.http.post<Payment>(this.url, payment, { responseType: 'text' as 'json'});
     
  }
  createContract(contract: Contract): Observable<any> {
   
    return this.http.post<Contract>(this.url_contracts, contract, { responseType: 'text' as 'json' });

  }
  deleteContract(Id: number): Observable<number> {
    return this.http.delete<number>(this.url_contracts + Id);
  }


  updatePayment(id: number, payment: Payment): Observable<Payment> {
    const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };
    return this.http.put<Payment>(this.url + id, payment, httpOptions);
  }

}
