import { Component, OnInit, } from '@angular/core';
import { FormBuilder, FormGroup, NgForm, Validators, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { Payment } from '../../../shared/payment';
import { Client } from 'src/shared/client';
import { Group } from 'src/shared/group';
import { Contract } from 'src/shared/contract';
import { Information } from 'src/shared/information';
import { Subscription } from 'src/shared/subscription';

import { PaymentService } from '../../payment.service';
import { SubscriptionService } from '../../subscription.service';
import { ClientService } from '../../client.service';

import { DatePipe } from '@angular/common';
import { MatSnackBar } from '@angular/material'; 
import { InformationService } from 'src/app/information.service';

@Component({
  selector: 'app-section-accept-payment',
  templateUrl: './section-accept-payment.component.html',
  styleUrls: ['./section-accept-payment.component.css']
})
export class SectionAcceptPaymentComponent implements OnInit {

  ExpireDate: Date;         // preset form expire date for +31 days from now as a default
  TodaysDate = new Date((new Date().getTime() + 3600000));                      

  dataSaved = false;
  message = null;

  paymentForm: any;
  contractForm: any;
  currentId: number;                                                  // selected client Id
  createdInformation: Information = null;

  payment: Payment;
  contract: Contract;

  subscriptions: Array<Subscription> = [];
  groups: Array<Group> = [];

  currentClient: Client;
  pickedSubscription: Subscription;
  pickedGroup: Group;
  p: number = 1;                          //pagination starting point


  constructor(private httpService: HttpClient,
    private formbulider: FormBuilder,
    private paymentService: PaymentService,
    private clientService: ClientService,
    private subscriptionService: SubscriptionService,
    private informationService: InformationService,
    private router: Router, public snackBar: MatSnackBar) { }


  openSnackBar(message: string, action: string) {
    this.snackBar.open(this.message, action, {
      duration: 4000,
      panelClass: ['snackbar']
    });
  } 

  ngOnInit() {

    this.ExpireDate = new Date(this.TodaysDate.getFullYear(), this.TodaysDate.getMonth() + 1, this.TodaysDate.getDate());

    if (this.TodaysDate.getMonth() == 11) { 
      this.ExpireDate.setFullYear(this.ExpireDate.getFullYear() + 1);
      this.ExpireDate.setMonth(0);
    }

    this.ExpireDate.setDate(this.ExpireDate.getDate() - 1);
  
   
    this.getSubscriptions();
    this.getGroups();
         
    this.currentId = this.paymentService.currentId;                  
    this.GetClientById(this.currentId);
  
    this.paymentForm = this.formbulider.group({
      SubscriptionExpires: new FormControl(this.ExpireDate),
      PaymentRegistered: [this.TodaysDate],
      ClientId: this.currentId,
      SubscriptionId: ['', [Validators.required]],
      AdditionalInformation: [''],
      CashRegistered: [0],
    });

    this.contractForm = this.formbulider.group({
      EntryDate: new FormControl(this.TodaysDate), 
      ClientId: this.currentId,
      GroupId: ['', [Validators.required]],
      
    });
    console.log("test");
    console.log(this.ExpireDate);
  }
  ngOnDestroy() {
    this.clientService.getClients();
  }
  passGroup(group: Group) {
    this.pickedGroup = group; 
  }
  addInformation(additionalInfo?: string) {
     
     this.createdInformation = {
        ClientId: this.currentId,
        display: true,
       Content: additionalInfo
      };


      this.informationService.addInformationToClient(this.createdInformation).subscribe(
        () => {
          this.dataSaved = true;
        }
      );
      
    }
 

  onFormSubmit() {
    this.dataSaved = false;

   
    const payment = this.paymentForm.value;

 
    
    if (payment['AdditionalInformation'] != null && payment['AdditionalInformation'].length > 1) {


      this.addInformation(payment['AdditionalInformation']);
    }
    
    this.CreatePayment(payment);
    this.paymentForm.reset(); 
  }

  onContractFormSubmit() { 
    this.dataSaved = false;
    const contract = this.contractForm.value;
    contract.paymentRequired = this.pickedGroup.contractRelatedGroup;
 
    this.CreateContract(contract);
  }


  CreatePayment(payment: Payment) { 
    this.paymentService.createPayment(payment).subscribe(
      () => {
        this.dataSaved = true;
        this.message = 'Pomyślnie przypisano nowy karnet klientowi.';
        this.paymentForm.reset();
        this.openSnackBar(this.message, 'Ok!');
        this.router.navigate(['clients']);
      }
    );


  }
  CreateContract(contract: Contract) {
    this.paymentService.createContract(contract).subscribe(
      () => {
        this.dataSaved = true;
        this.message = 'Klient został przypisany do grupy.';
        this.contractForm.reset();
        this.openSnackBar(this.message, 'Ok!'); 
      }
    ); 
  }
  getSubscriptions() {
    this.subscriptionService.getSubscriptions().subscribe(subscriptionsData => {
      this.subscriptions = subscriptionsData; 
    });
  }
  getGroups() {
    this.subscriptionService.getGroups().subscribe(data => {
      this.groups = data; 
    });
  } 
  GetClientById(clientId: number) {
    this.clientService.getClientById(clientId).subscribe((clientData: Client) => {
      this.currentClient = clientData; 
    });
  }


  ZerujPrzyjetoDoKasy() {
    this.paymentForm['CashRegistered'] = "";
  }

}
