import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { Subscription } from '../../../shared/subscription';
import { Group } from '../../../shared/group';
import { SubscriptionService } from '../../subscription.service';
import { Observable } from 'rxjs';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-section-add-subscription',
  templateUrl: './section-add-subscription.component.html',
  styleUrls: ['./section-add-subscription.component.css']
})
export class SectionAddSubscriptionComponent implements OnInit {

  dataSaved = false;
  message = null;

  subscriptionForm: any;
  groupForm: any;

  subscriptionsData: any[];
  groupsData: any[];

  constructor(private httpService: HttpClient, private formbulider: FormBuilder, private subscriptionService: SubscriptionService, public snackBar: MatSnackBar) { }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(this.message, action, {
      duration: 4000,
      panelClass: ['snackbar']
    });
  }

  getSubscriptions() {

    this.subscriptionService.getSubscriptions().subscribe(data => {
      this.subscriptionsData = data;
      this.message = 'Pomyślnie pobrano spis karnetów.';


    });
  }

  getGroups() {

    this.subscriptionService.getGroups().subscribe(data => {
      this.groupsData = data;
      this.message = 'Pomyślnie pobrano spis karnetów.';


    });
  }

  ngOnInit() {
    this.getSubscriptions();
    this.getGroups();

    this.subscriptionForm = this.formbulider.group({
      Name: ['', [Validators.required]],
      Price: ['', [Validators.required]],
      GymEntries: ['', [Validators.required]],
      MartialArtsEntries: ['', [Validators.required]],
    });
    this.groupForm = this.formbulider.group({
      Name: ['', [Validators.required]],
      Abreviation: ['', [Validators.required]],
      ContractRelatedGroup: ['', [Validators.required]],

    });

  }


  onFormSubmit() {
    this.dataSaved = false;
    const subscription = this.subscriptionForm.value;
    this.CreateSubscription(subscription);
    this.subscriptionForm.reset();
    this.getSubscriptions();
  }
  onGroupFormSubmit() {
    this.dataSaved = false;
    const group = this.groupForm.value;
    this.CreateGroup(group);
    this.groupForm.reset();
    this.getGroups();
  }


  CreateSubscription(subscription: Subscription) {

    this.subscriptionService.createSubscription(subscription).subscribe(
      () => {
        this.dataSaved = true;
        this.message = 'Pomyślnie utworzono nowy karnet.';
        this.subscriptionForm.reset();

        this.openSnackBar(this.message, 'Ok!');
      }
    );


  }
  CreateGroup(group: Group) {

    this.subscriptionService.createGroup(group).subscribe(
      () => {
        this.dataSaved = true;
        this.message = 'Pomyślnie utworzono nowy karnet.';
        this.subscriptionForm.reset();

        this.openSnackBar(this.message, 'Ok!');
      }
    );


  }
  resetForm() {
    this.subscriptionForm.reset();
    this.message = null;
    this.dataSaved = false;
  }





}
